methods: {
hello(i) {
setTimeout(() => {
if (i.types === "") {
this.workStateSelect(i.name);
}
if (i.types === "") {
this.worktypeSelect(i.name);
}
if (i.types === "") {
this.rl = i.name;
}
});
},
clearFilter() {
console.log(1);
this.param = {
workState: "",
beginDate: "",
endDate: "",
workType: ""
};
this.tabTextIndex = 0;
this.rl = "";
this.clearInit();
this.$refs["filterSele"].clearClass(this.filterArr);
},
toTime(date) {
setTimeout(() => {
let times = date["beginDate"].replace(/-/g, "/");
let tis = new Date(times).getTime();
let timee = date["endDate"].replace(/-/g, "/");
let tie = new Date(timee).getTime();
if (tis > tie) {
this.$vux.toast.show({
type: "warn",
text: ""
});
return;
}
this.param.beginDate = `${date["beginDate"]}:00`;
this.param.endDate = `${date["endDate"]}:00`;
});
},
tabLeft() {},
tabRight() {
this.$router.push({ path: "/AddTask" });
},
tabClick(index) {
setTimeout(() => {
this.tabTextIndex = index;
});
},
doworkTabClick(index) {

switch (index) {
case 0:
this.workStaateSelShow = true;
break;
case 1:
this.timeSelShow = true;
break;
case 2:
this.worktypeSelShow = true;
break;
case 3:
this.modelSelShow = true;
break;
}
},
workStateSelect(value) {
this.workStaateSelShow = false;
this.doWorkTab[0] = value;
this.param.workState = value;
},
timeSelect(value) {
this.timeSelShow = false;
this.doWorkTab[1] = value;
this.param.day = value;
},
worktypeSelect(value) {
this.worktypeSelShow = false;
this.doWorkTab[2] = value;
this.param.workType = value;
},
modelSelect(value) {
this.modelSelShow = false;
this.doWorkTab[3] = value;
},
clearInit() {
